import { NextResponse } from 'next/server';
import { prisma } from '@/lib/prisma';
import bcrypt from 'bcryptjs';
import { getServerSession } from 'next-auth';
import { authOptions } from '@/lib/auth';
import { signIn } from 'next-auth/react';

// Define the expected request body type
interface LoginRequest {
  email: string;
  password: string;
}

// This route handles the login process by verifying credentials
// and creating a session using NextAuth.js

// Helper function to set CORS headers
const setCorsHeaders = (response: NextResponse) => {
  const origin = process.env.NEXTAUTH_URL || 'http://localhost:3000';
  response.headers.set('Access-Control-Allow-Origin', origin);
  response.headers.set('Access-Control-Allow-Methods', 'GET, POST, OPTIONS');
  response.headers.set('Access-Control-Allow-Headers', 'Content-Type, Authorization');
  response.headers.set('Access-Control-Allow-Credentials', 'true');
  return response;
};

// This is a simplified login route that works with NextAuth's built-in credential authentication
export async function POST(request: Request) {
  // Parse the request body
  let body: LoginRequest;
  try {
    body = await request.json();
  } catch {
    console.error('‚ùå Invalid JSON in request body');
    return setCorsHeaders(
      NextResponse.json(
        { success: false, error: 'Invalid request body' },
        { status: 400 }
      )
    );
  }

  const { email, password } = body;
  
  // Validate input
  if (!email || !password) {
    return setCorsHeaders(
      NextResponse.json(
        { success: false, error: 'Email and password are required' },
        { status: 400 }
      )
    );
  }

  console.log('üîë Login request received for:', email);
  
  try {
    // Find user by email
    const user = await prisma.user.findUnique({
      where: { email },
    });

    if (!user) {
      console.log('‚ùå User not found:', email);
      return setCorsHeaders(
        NextResponse.json(
          { success: false, error: 'Invalid email or password' },
          { status: 401 }
        )
      );
    }

    // Verify password
    const isPasswordValid = await bcrypt.compare(password, user.password);
    if (!isPasswordValid) {
      console.log('‚ùå Invalid password for user:', email);
      return setCorsHeaders(
        NextResponse.json(
          { success: false, error: 'Invalid email or password' },
          { status: 401 }
        )
      );
    }

    // Create a session with NextAuth
    await getServerSession(authOptions);
    
    // Create a response with the user data (excluding password)
    const { password: _password, ...userData } = user;
    
    // Create a response with the user data
    const response = NextResponse.json({
      success: true,
      user: {
        id: userData.id,
        name: userData.name,
        email: userData.email,
        role: userData.role,
      },
    });

    // Set auth cookies manually since we're not using NextAuth's signIn
    const token = await signIn('credentials', {
      redirect: false,
      email,
      password,
      callbackUrl: '/admin'
    });

    if (token?.error) {
      console.error('‚ùå NextAuth signIn error:', token.error);
      throw new Error(token.error);
    }

    return setCorsHeaders(response);

  } catch (error) {
    console.error('‚ùå Error during login:', error);
    return setCorsHeaders(
      NextResponse.json(
        { 
          success: false, 
          error: error instanceof Error ? error.message : 'An error occurred during login' 
        },
        { status: 500 }
      )
    );
  }
}

// Handle OPTIONS method for CORS preflight requests
export async function OPTIONS() {
  console.log('üîÑ Login OPTIONS preflight request');
  const response = new NextResponse(null, { status: 204 });
  return setCorsHeaders(response);
}
